<template>
  <section class="testimonials" id="testimonials">
    <h2>Témoignages</h2>

    <div class="slider-container">
      <!-- Flèches -->
      <button class="nav-btn left" @click="prevSlide">‹</button>
      <button class="nav-btn right" @click="nextSlide">›</button>

      <div class="slider-wrapper" ref="wrapper">
        <div
          class="slider"
          ref="slider"
          :style="{ transform: `translateX(-${translateX}px)` }"
        >
          <div
            v-for="(t, index) in loopTestimonials"
            :key="index"
            class="slide"
          >
            <blockquote>
              <p>{{ t.text }}</p>
              <footer>— {{ t.author }}</footer>
            </blockquote>
          </div>
        </div>
      </div>
    </div>

    <!-- Points -->
    <div class="dots">
      <span
        v-for="(dot, index) in testimonials.length"
        :key="index"
        :class="{ active: index === currentIndex }"
      ></span>
    </div>
  </section>
</template>

<script>
export default {
  name: "Testimonials",
  data() {
    return {
      testimonials: [
        {
          text: "Avec DariandCo, le suivi de notre stock est devenu un jeu d’enfant. La scannette nous fait gagner un temps fou et les erreurs d’inventaire ont presque disparu.",
          author: "Youssef, Responsable logistique à Casablanca",
        },
        {
          text: "J’utilise DariandCo tous les jours dans ma boutique. Même sans scannette, la saisie manuelle est rapide et fluide. Les rapports de ventes m’aident à mieux anticiper mes commandes.",
          author: "Salma, Gérante de magasin à Rabat",
        },
        {
          text: "Les tableaux de bord sont clairs et intuitifs. En quelques clics, je vois mes ventes, mes stocks et mes produits les plus rentables. Un vrai gain de visibilité pour mon activité.",
          author: "Karim, Entrepreneur à Marrakech",
        },
        {
          text: "Depuis qu’on a installé DariandCo dans notre entrepôt, la gestion des stocks et des livraisons est devenue beaucoup plus simple. L’équipe a adopté l’outil dès la première semaine.",
          author: "Nadia, Gestionnaire d’entrepôt à Tanger",
        },
        {
          text: "Les statistiques et prévisions de ventes sont précises. J’ajuste mes achats chaque semaine grâce au tableau de bord. C’est une vraie aide à la décision pour mes approvisionnements.",
          author: "Hassan, Propriétaire d’épicerie à Fès",
        },
        {
          text: "L’interface est fluide et rapide pour tous les employés.",
          author: "Amina, Agadir",
        },
      ],
      translateX: 0,
      slideWidth: 0,
      currentIndex: 0,
      speed: 0.5, // px par frame
      raf: null,
    };
  },
  computed: {
    loopTestimonials() {
      return [...this.testimonials, ...this.testimonials];
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.slideWidth = this.$refs.slider.children[0].offsetWidth;
      this.startAutoScroll();
    });
    window.addEventListener("resize", this.updateSlideWidth);
  },
  beforeUnmount() {
    cancelAnimationFrame(this.raf);
    window.removeEventListener("resize", this.updateSlideWidth);
  },
  methods: {
    updateSlideWidth() {
      this.slideWidth = this.$refs.slider.children[0].offsetWidth;
    },
    startAutoScroll() {
      const step = () => {
        this.translateX += this.speed;
        const totalWidth = this.slideWidth * this.testimonials.length;
        if (this.translateX >= totalWidth) {
          this.translateX = 0;
        }
        this.currentIndex =
          Math.floor(this.translateX / this.slideWidth) %
          this.testimonials.length;
        this.raf = requestAnimationFrame(step);
      };
      this.raf = requestAnimationFrame(step);
    },
    prevSlide() {
      this.translateX -= this.slideWidth;
      if (this.translateX < 0) {
        this.translateX += this.slideWidth * this.testimonials.length;
      }
    },
    nextSlide() {
      this.translateX += this.slideWidth;
      const totalWidth = this.slideWidth * this.testimonials.length;
      if (this.translateX >= totalWidth) {
        this.translateX = 0;
      }
    },
  },
};
</script>

<style scoped>
.testimonials {
  padding: 60px 20px;
  background-color: #f4f4f4;
  text-align: center;
  overflow: hidden;
}
.testimonials h2 {
  font-size: 2em;
  color: var(--primary);
  margin-bottom: 40px;
}

.slider-container {
  position: relative;
  max-width: 1100px;
  margin: 0 auto;
}

.slider-wrapper {
  overflow: hidden;
}

.slider {
  display: flex;
  flex-wrap: nowrap;
  transition: transform 0.2s linear;
}

.slide {
  flex: 0 0 calc(100% / 3);
  padding: 0 15px;
  box-sizing: border-box;
}

blockquote {
  background-color: white;
  padding: 25px 30px;
  border-radius: 12px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
  font-style: italic;
  color: var(--text-dark);
  line-height: 1.5;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
blockquote footer {
  margin-top: 15px;
  font-style: normal;
  font-weight: 600;
  color: var(--primary-dark);
  text-align: right;
}

/* Flèches */
.nav-btn {
  background: #96154a;
  color: white;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 28px;
  cursor: pointer;
  transition: background-color 0.3s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.nav-btn.left {
  left: -60px;
}
.nav-btn.right {
  right: -60px;
}
.nav-btn:hover {
  background-color: #ff5c84;
}

/* Dots */
.dots {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  gap: 10px;
}
.dots span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ccc;
  display: inline-block;
  cursor: pointer;
  transition: background-color 0.3s;
}
.dots span.active {
  background-color: #ff5c84;
}

/* Responsive */
@media (max-width: 1024px) {
  .slide {
    flex: 0 0 50%;
  }
}
@media (max-width: 768px) {
  .slide {
    flex: 0 0 100%;
  }
  .nav-btn.left {
    left: 10px;
  }
  .nav-btn.right {
    right: 10px;
  }
}
</style>
