<script>
import logo from "./assets/logo.webp";
import Testimonials from "./components/Testimonials.vue";
import Swal from "sweetalert2";

export default {
  name: "LandingPage",
  data() {
    return {
      logo,
      contact: { name: "", email: "", message: "" },
      showPopup: false,
      showDemoModal: false,
      showLoginInfo: false,
      selectedDemo: "",

      // 🔹 Nouveaux articles de blog
      blogPosts: [
        {
          excerpt:
            "Découvrez les nouvelles stratégies et outils pour optimiser la gestion des stocks en entreprise cette année.",
          image: "/images/blog1.png",
          link: "/blog/gerer-son-stock-2026.html",
        },
        {
          title: "Les 5 erreurs courantes en gestion d’inventaire",
          excerpt:
            "Ces erreurs freinent la croissance de nombreuses entreprises. Apprenez à les éviter pour améliorer votre rentabilité.",
          image: "/images/blog2.png",
          link: "/blog/erreurs-gestion-inventaire.html",
        },
        {
          excerpt:
            "Découvrez FlowStock, le logiciel gratuit idéal pour gérer vos stocks simplement et optimiser votre activité.",
          image: "/images/blog3.png",
          link: "/blog/comparatif-logiciels-stock.html",
        },
      ],
    };
  },
  components: { Testimonials },
  methods: {
    openDemo(videoFile) {
      this.selectedDemo = `/${videoFile}`; // chemin depuis la racine
      this.showDemoModal = true;
    },
    goToTrial() {
      const exeUrl =
        "https://drive.google.com/uc?export=download&id=14KRzfWq899xRogBghqUk1MJMz5__icM4";
      const link = document.createElement("a");
      link.href = exeUrl;
      link.download = "FlowStock Setup 1.0.0.exe"; // nom du fichier téléchargé
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

      this.showPopup = true;
    },
    closePopup() {
      this.showPopup = false;
      setTimeout(() => {
        this.showLoginInfo = true;
      }, 300);
    },
    closeLoginInfo() {
      this.showLoginInfo = false;
    },
    closeDemo() {
      this.showDemoModal = false;
      this.selectedDemo = "";
    },
    async submitForm() {
      if (!this.contact.name || !this.contact.email || !this.contact.message) {
        await Swal.fire({
          icon: "warning",
          title: "Champs manquants",
          text: "Merci de remplir tous les champs avant d’envoyer.",
          confirmButtonColor: "#96154a",
        });
        return;
      }

      const phoneNumber = "212708059377";
      const message = encodeURIComponent(
        `Bonjour, je suis ${this.contact.name}.\nEmail: ${this.contact.email}\nMessage: ${this.contact.message}`
      );
      const whatsappUrl = `https://wa.me/${phoneNumber}?text=${message}`;

      window.open(whatsappUrl, "_blank");

      await Swal.fire({
        icon: "success",
        title: "Message prêt à être envoyé !",
        text: "Vous allez être redirigé vers WhatsApp pour nous contacter.",
        confirmButtonColor: "#96154a",
        timer: 3000,
        timerProgressBar: true,
      });

      this.contact = { name: "", email: "", message: "" };
    },

    // 🔹 scrollToSection doit être ici, dans methods
    scrollToSection(id) {
      const section = document.getElementById(id);
      if (section) {
        section.scrollIntoView({
          behavior: "smooth",
          block: "start",
        });
      }
    },
  },
  mounted() {
    // Empêche le menu contextuel et Ctrl+U (source) — dissuasif seulement
    this._preventContext = (e) => e.preventDefault();
    this._preventKeys = (e) => {
      // Désactive Ctrl+U, Ctrl+Shift+I, F12 (devtools) et Ctrl+S / Ctrl+P si souhaité
      if (e.ctrlKey && (e.key === "u" || e.key === "U")) {
        e.preventDefault();
      }
      if (e.ctrlKey && e.shiftKey && (e.key === "I" || e.key === "i")) {
        e.preventDefault();
      }
      if (e.key === "F12") {
        e.preventDefault();
      }
    };

    document.addEventListener("contextmenu", this._preventContext);
    document.addEventListener("keydown", this._preventKeys);
  },
  beforeUnmount() {
    document.removeEventListener("contextmenu", this._preventContext);
    document.removeEventListener("keydown", this._preventKeys);
  },
};
</script>

