.price-box .currency {
  font-size: 0.7em; /* plus petit que le prix */
  vertical-align: super; /* décalage vers le haut */
  margin-left: 2px;
}

.current-price {
  font-size: 2rem;
  font-weight: 700;
  color: #96154a;
  margin-right: 10px;
}

/* Featured button */
.featured-btn {
  background: linear-gradient(45deg, #96154a, #ff5c84);
  box-shadow: 0 4px 12px rgba(255, 92, 132, 0.4);
  padding: 14px 28px;
}

/* Subtitle */
.subtitle {
  margin: 10px 0;
  font-weight: 500;
  color: #333;
}
.video-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.video-modal-box {
  position: relative;
  width: 80%;
  max-width: 800px;
  background: #000;
  padding: 10px;
  border-radius: 10px;
}

.video-modal-box video {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.close-btn {
  position: absolute;
  top: 5px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  color: #fff;
  cursor: pointer;
}
.account {
  background: #f9f9f9;
  padding: 1em;
  border-radius: 8px;
  margin: 1em 0;
  border-left: 4px solid #007bff;
}

.account code {
  background: #eee;
  padding: 0.2em 0.4em;
  border-radius: 4px;
  font-weight: 500;
}

.note {
  margin-top: 1em;
  font-style: italic;
  color: #555;
}
/* BLOG SECTION */
.blog {
  padding: 60px 20px;
  background-color: #fff;
  text-align: center;
}

.blog h2 {
  font-size: 2.2rem;
  color: #96154a;
  margin-bottom: 40px;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  max-width: 1000px;
  margin: 0 auto;
}

.blog-card {
  background: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}

.blog-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.blog-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.blog-card h3 {
  font-size: 1.3rem;
  margin: 15px;
  color: #333;
}

.blog-card p {
  margin: 0 15px 15px;
  color: #555;
  font-size: 0.95rem;
  line-height: 1.5;
}

.read-more {
  display: inline-block;
  margin: 0 15px 20px;
  color: #96154a;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s;
}

.read-more:hover {
  color: #ff5c84;
}
.download-note {
  font-size: 0.95rem;
  color: #b03a2e; /* bordeaux professionnel */
  background-color: #f9e6e4; /* fond léger pour contraste */
  padding: 10px 15px;
  border-left: 4px solid #96154a; /* accent couleur FlowStock */
  border-radius: 5px;
  margin-top: 10px;
  line-height: 1.4;
}
</style>
